import React from 'react'
import WeatherHour from './WeatherHour.jsx'

export default function WeatherDay() {
    function SmallLine() {
        return (
            <div className='weather-day__small-line'></div>
        )
    }
    function Line() {
        return (
            <div className='weather-day__line'></div>
        )
    }
    return (
        <div className='weather-app__weather-day'>
            <SmallLine />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <Line />
            <WeatherHour />
            <SmallLine />
        </div>
    )
}